import React, { Component } from 'react';
import Modal from 'react-modal';

const customStyles = {
    content: {
        top: '50%',
        left: '50%',
        right: 'auto',
        bottom: 'auto',
        marginRight: '-50%',
        transform: 'translate(-50%, -50%)'
    }
};

Modal.setAppElement('#root')

class Jumbotron extends Component {
    constructor(props) {
        super(props);
        this.state = {
            modalIsOpen: false
        };
        this.openModal = this.openModal.bind(this);
        this.afterOpenModal = this.afterOpenModal.bind(this);
        this.closeModal = this.closeModal.bind(this);
    }

    openModal() {
        this.setState({ modalIsOpen: true });
    }

    afterOpenModal() {
        // this.subtitle.style.color = '#f00';
    }

    closeModal() {
        this.setState({ modalIsOpen: false });
    }

    render() {
        return (
            <section className="jumbotron text-center mb-0 bg-white">
                <div className="container">
                    <h1 className="jumbotron-heading">Vit Nam 1915 - 1920</h1>
                </div>
                <p className="lead text-muted">
                    Nm 1909, Albert Kahn, mt ch nh bng triu ph - cng l mt nh hot ng t thin ni ting ngi Php, bt u tin hnh mt d n y tham vng: xy dng kho t liu nh mu cho tt c cc dn tc trn th gii. Ti Vit Nam, ngi c giao nhim v cm my l Leon Busy, mt trung y hu cn ca qun i l dng. i khp Bc K, Leon chp khong 1700 bc nh VN trong thi gian 1915- 1920. "Ci ng phc nht  Albert Kahn l s say m v... chu chi. Bi, k thut ra nh mu hi  rt phc tp, i hi cn c x l ngay bng cng ngh tin tin nht trc khi gi v Php" - KTS on Bc, vn c d lun bit ti nh mt trong nhng nh su tp nh H Ni c, nhn xt - "V, chng ta cng xc ng hn khi bit Albert phi chu cnh ph sn vo nm 1932 v mt trong i ngho, cn b su tp nh th b sung cng v tr thnh c s d liu ca Bo tng Albert Kahn ti Php by gi. Trong album ny cn c mt s nh mu ca W. Robert More chp.
                </p>
                <a className="btn btn-outline-primary" onClick={this.openModal}>V Albert Kahn</a>
                <Modal
                    isOpen={this.state.modalIsOpen}
                    onAfterOpen={this.afterOpenModal}
                    onRequestClose={this.closeModal}
                    style={customStyles}
                    contentLabel="V Alber Kahn">
                    <div className="container">
                        <div className="row">
                            <div className="col-10 offset-1">
                                <div className="card">
                                    <div className="card-body">
                                        <h5 className="card-title text-center">Albert Kahn</h5>
                                        <span className="text-muted text-center">Sinh 3 thng Ba nm 1860  Mt 14 thng Mi Mt nm 1940)</span>
                                        <p className="card-text"> ng l mt ngi Php, ch nh bng, mt ngi lm thin nguyn, c bit n nh khi xng d n xy dng kho t liu nh mu cho tt c cc dn tc trn th gii (The Archives of the Planet). D n ny ko di 22 nm, thu v mt b su tp l 72 nghn bc nh mu, v 183 nghn mt phim.</p>
                                        <p className="card-text">ng sinh ra  Bas-Rhin, Php, l ngi con c trong 1 gia nh c 4 ngi con. Cha ng l Louis Kahn, mt ngi Do Thi bun bn gia sc. M ng l Babette Kahn (sinh  Bloch), khng c i hc. B m ca Kahn mt khi ng ln mi tui. Following the German annexation of Alsace-Lorraine in 1871, the Kahn family moved to Saint-Mihiel in north-eastern France in 1872 where he continued his studies at the Collge de Saverne from 1873 to 1876. In 1879. Kahn became a bank clerk in Paris, but studied for a degree in the evenings. His tutor was Henri Bergson, who became his lifelong friend. He graduated in 1881 and continued to mix in intellectual circles, making friends with Auguste Rodin and Mathurin Mheut. In 1892 Kahn became a principal associate of the Goudchaux Bank, which was then regarded as one of the most important financial houses of Europe. He also promoted higher education through travelling scholarships. Vo cui i, cuc i khng hong kinh t  lm khnh kit ng, cng nh buc ng phi dng d n ca mnh. ng cht ti Boulogne-Billancourt, Hauts-de-Seine, France ngy 14 thng Mi Mt 1940 khi nc Php vn ang di s chim ng ca Pht xt c.</p>
                                    </div>

                                </div>
                            </div>
                        </div>
                    </div>
                    <button onClick={this.closeModal}>close</button>
                </Modal>
            </section>
        )
    }

};

export default Jumbotron;


