import React, { Component } from 'react';
import Card from './Card';

const imageData = [
    {
        name: "vietnam_001.jpg",
        desc: "Cp o kp trong mt gnh tung c.",
        takenYear: "1915"
    },
    {
        name: "vietnam_002.jpg",
        desc: "Mt gnh tung c.",
        takenYear: "1915"
    },
    {
        name: "vietnam_003.jpg",
        desc: "Ngh s tung c ti H Ni",
        takenYear: "1916"
    },
    {
        name: "vietnam_004.jpg",
        desc: "Nhng c gi ng vai con Tt trong mt vn c ngi.",
        takenYear: "1920"
    },
    {
        name: "vietnam_005.jpg",
        desc: "Ni c",
        takenYear: "1915"
    },
    {
        name: "vietnam_006.jpg",
        desc: "Mt vin quan i thn triu Nguyn ti H Ni",
        takenYear: "1920"
    },
    {
        name: "vietnam_007.jpg",
        desc: "Quan Huyn v nhn vin chnh quyn ti mt ngi nh",
        takenYear: "1915"
    }
];

class Album extends Component {

    render() {

        const cardItems = imageData.map((image) => {
            return <Card 
                        key={image.name} 
                        info={image} 
                    />
        });

        return (
            <div className="album py-5 bg-light">
                <div className="container">
                    <div className="row">
                        {cardItems}
                    </div>

                </div>
            </div>
        )
    }

};

export default Album;


